/************
RESETS
************/
html {
    box-sizing: border-box;
    color: #444;
    font: 12pt 'Open Sans', Arial;
    font-weight: 400;
}

*, *::before, *::after {
    box-sizing: inherit;
    color: inherit;
    font: inherit;
}

.background-mogs-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    overflow: hidden;
    width: 100%;
    height: 100%;
}

.background-cover {
    position: absolute;
    background: rgba(252, 136, 29, 0.7);
    top: 0;
    bottom: 0;
    width: 100%;

}

.background-mogs-container .mog-img {
    margin: 10px;
    position: relative;
    background-size: cover;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: solid #FEBF44 2px;
    -webkit-box-shadow: 0px 10px 30px -8px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 10px 30px -8px rgba(0,0,0,0.75);
    box-shadow: 0px 10px 30px -8px rgba(0,0,0,0.75);
}

body {
	margin: 0;
	padding: 0;
	background-color: white;
    padding-bottom: 1em;
}

body.login-body {
    margin: -25px 0 0 0;
    padding: 0;
    background-color: white;
    overflow: hidden;
}

navbar {
	width: 100%;
  	top: -16px;
}

navbar ul {
    display: flex;
    text-align: center;
    margin: 0;
    padding: 0;
    background-color: #FC881D;
    -webkit-box-shadow: 0px 4px 5px 0px rgba(0,0,0,0.3);
    -moz-box-shadow: 0px 4px 5px 0px rgba(0,0,0,0.3);   
    box-shadow: 0px 4px 5px 0px rgba(0,0,0,0.3);
}

navbar ul li {
	list-style: none;	
	flex: 1;
    border-left: 1px solid gray;
}

.nav-selected {
	border-bottom: solid yellow 5px;
}

a {
    text-decoration: none;
}

.selected-mog-url a {
	    
    color: blue;
}

.navtab {
    -webkit-flex: 1;
    -ms-flex: 1;
    flex: 1;
    margin: 0;
    color: white;
    font-size: 1.3em;
    padding: 0.7em;
}

/*******
LOGIN SCREEN CSS
****/
.login-container-content {
    position: relative;
    width: 25em;
    height: 25em;
}

.login-container {
    overflow: hidden;
    width: 400px;
    background: rgba(238, 238, 238, 1);
    height: 480px;
    position: absolute;
    border-radius: 2%;
    top: 50%;
    left: 50%;
    vertical-align: baseline;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    box-shadow: 0px 8px 54px -11px black;
}

.register {
    display: inline-block;
    margin: 0 10px;
}

.login {
    display: inline-block;
    margin: 0 10px;
}


.login:hover, .register:hover {
    cursor:pointer
}

.login-selected {
    color: #FC881D;
}

.fields-container input {
    padding: 5px 0px 5px 5px;
    margin-bottom: 6px;
    border-radius: 5px;
    outline: none;
    border: 1px solid #bbb;
}

.register-login {
    position: absolute;
    font-size: 1.1em;
    top: 230px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    width: 200px;
    text-align: center;
}

.logo-container {
    text-align: center;
    position: absolute;
    top: 20px;
    left: 50%;
    -webkit-transform: translatex(-50%);
    transform: translatex(-50%);
}

.logo-container > img {
    width: 185px;
    height: 185px;
}

.fields-container {
    width: 270px;
    height: 175px;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-justify-content: space-around;
    -ms-flex-pack: distribute;
    justify-content: flex-start;
    top: 255px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    padding: 10px;
    border-radius: 5px;
}

#remember-me {
    position: absolute;
    top: 337px;
    left: 72px;
    width: 110px;
    font-size: 11px;
}

.btn-submit {
    top: 418px;
    padding: 10px 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    position: absolute;
    outline: 0;
    border: none;
    width: 250px;
    background: #FC881D;
    border-radius: 5px;
    color: white;
}

