.game-field {
    margin: 3em auto;
    min-width: 480px;
    width: 85%;
    height: 600px;
    background-color: white;
    position: relative;
    overflow: hidden;
    border-bottom: solid black 1px;
}

.mog-drop-container {
    width: 100%;
    height: 100%;
    position: absolute;
}

.slammer-explosion {
    position: relative;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
}

.slammer-game-results {
    text-align:center;
}

.slammer-game-results h3 {
    font-size: 1.5em;
}

.explosion-container {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
}

.explosion {
    background: url("/images/memeslam.png");
    width: 300px;
    height: 300px;
    background-color: white;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center; 
}

.stack-itm-contr {
     position: absolute;
    -webkit-transform: rotate3d(1,0, 0,60deg);
}

.mog-stack {
    position: relative;
    width: 150px;
    height: 600px;
    margin: auto;
}

.drop-item {
    position: absolute;
    width: 100px;
    height: 100px;
    border: 2px solid orange;
    border-radius: 50%;
    background-size: cover;
    background-color: white;
    background-position: center;
    box-shadow: 5px 5px 30px -9px rgba(102, 102, 102, 0.5);
}

.stack-item:first-child {
    -webkit-box-shadow: 15px 10px 20px 1px rgba(173,173,173,1);
    -moz-box-shadow: 15px 10px 20px 1px rgba(173,173,173,1);
    box-shadow: 15px 10px 20px 1px rgba(173,173,173,1);
}

.stack-item:last-child {
    background: url('/images/memeslam.png');
    background-color: white;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
}

.stack-item {
    position: absolute;
    width: 150px;
    height: 150px;
    border: 2px solid orange;
    border-radius: 50%;
    -webkit-transform: rotate3d(1, 0, 0, 60deg);
    box-shadow: 0px 1px 0px 1px #e8e8e8; 
}

.display-stack h3 {
    width: 400px;
    position: absolute;
    text-align: center;
    left: 50%;
    transform: translateX(-50%);
    top: 50px;
    font-size: 3em;
}

.game-details h3 {
    padding-left: 3em;
}
.user-mogs, .won-mogs{
    margin: auto;
    width: 85%;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    min-height: 120px;
    background-color: #EEEEEE;
    border-radius: 10px;
}

/****
SLAMMER CSS
***/
.slammer-container {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateX(-50%) translateY(-50%);
}

.slammer-container > h3 {
    font-size: 3em;
}

.slammer {
    width: 400px;
    position: relative;
}

.upper  {
    position: absolute;
    top: -170px;
    height: 200px;
    width: 400px;
    background-color: black;
    border-radius: 400px 400px 0 0;
    -moz-border-radius: 400px 400px 0 0;
    -webkit-border-radius: 400px 400px 0 0;
}

.upper:hover, .lower:hover {
    background-color: red;
}

.lower{
    height: 200px;
    position: absolute;
    width: 400px;
    background-color: black;
    border-radius: 0 0 400px 400px;
    -moz-border-radius: 0 0 400px 400px;
    -webkit-border-radius: 0 0 400px 400px;
    top: 30px;
}

.gates {
    height: 60px;
    width: 400px;
    background-color: white;
    position: relative;
}

.enter {
    float: left;
    position: absolute;
    height: 60px;
    width: 375px;
    background-color: white;
    background-image: url('/images/mouseguide.png');
    z-index: 1;
}

.exit {
    float: right;
    position: absolute;
    height: 60px;
    width: 25px;
    background-color: white;
    right: 0px;
    z-index: 1;
}

::-webkit-input-placeholder {
   padding-left: 5px;
}

.pre-search, .prompt-accept, .game-search, .attempt-join,.join-successful {
    text-align: center;
    margin: auto;
    font-size: 1.5em;
}

.search-for-match {
    background-color: #FDC045;
    border: none;
    border-radius: 5px;
    color: white;
    padding: 0.5em;
}

.search-for-match:active {
    background-color: orange;
}